<template>
  <main>
    <h1>Add Consumer</h1>
    <form @submit="submit()" @submit.prevent method="POST">
      <img src="honey.jpeg" alt="honey" />

      <img src="default_profile_picture.png" alt="default_profile_picture" />
      <input
        type="text"
        name="username"
        v-model="consumer.username"
        placeholder=" enter username"
        required
      />
      <input
        type="email"
        name="email"
        v-model="consumer.email"
        placeholder=" enter email"
        required
      />
      <input
        type="text"
        name="nameSurname"
        v-model="consumer.nameSurname"
        placeholder=" enter name and surname"
        required
      />
      <input
        type="password"
        name="password"
        v-model="consumer.password"
        placeholder=" enter password"
        required
      />
      <input type="submit" value="SUBMIT" />
    </form>
  </main>
</template>

<script>
import Axios from "axios";

export default {
  name: "AddConsumer",
  data: function () {
    return {
      consumer: {},
    };
  },
  watch: {
    "consumer.username": function () {
      let regex = /^[a-zA-Z]{6}[0-9]{2}[abcdehlmprstABCDEHLMPRST]{1}[0-9]{2}([a-zA-Z]{1}[0-9]{3})[a-zA-Z]{1}$/i;
      if (regex.exec(this.consumer.username)) {
        document.querySelector("input[type='submit']").disabled = false;
      } else {
        document.querySelector("input[type='submit']").disabled = true;
      }
    },
  },
  methods: {
    submit: function () {
      Axios.post(
        `${process.env.VUE_APP_APIROOT}uploaderarea/consumer`,
        this.consumer
      )
        .then(() => {
          alert("consumer added correctly!");
          location.reload();
        })
        .catch((error) => {
          if (error.response.status === 409) {
            alert("the username already exist");
          } else {
            alert("something went wrong");
          }
        });
    },
  },
};
</script>

<style scoped>
@media screen and (orientation: landscape) {
  form {
    display: flex;
    flex-flow: column wrap;
    align-items: center;
    max-width: 900px;
    margin-right: auto;
    margin-left: auto;
    width: 60%;
    height: 70vh;
    background-color: white;
  }

  form > img[alt="honey"] {
    height: 100%;
    width: 50%;
  }

  form > img[alt="default_profile_picture"] {
    box-sizing: border-box;
    margin: 3vh 20%;
    height: 18%;
  }

  form > input[type="text"],
  form > input[type="email"],
  form > input[type="password"] {
    flex-basis: 8%;
    width: 35%;
    margin-bottom: 3.5vh;
    border-top: none;
    border-right: none;
    border-left: none;
    border-radius: 5px;
    background-color: whitesmoke;
  }

  form > input[type="text"]:focus,
  form > input[type="email"]:focus,
  form > input[type="password"]:focus,
  form > input[type="submit"]:focus {
    outline: 0;
  }

  form > input[type="submit"] {
    box-sizing: border-box;
    width: 30%;
    height: 7vh;
    font-size: 3vh;
    font-weight: bolder;
    background-color: rgba(0, 0, 0, 0.83);
    border: 4px solid black;
    border-radius: 10px;
    color: orange;
  }

  form > input[type="submit"]:disabled {
    background-color: rgb(206, 206, 206);
    color: rgb(160, 160, 160);
    border: 1px solid grey;
    cursor: not-allowed;
    font-weight: lighter;
  }

  input[type="submit"]:hover {
    cursor: pointer;
  }
}

@media screen and (orientation: portrait) {

  main{
    height: 60vh;
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
    align-content: center;
  }

  form {
    display: flex;
    flex-flow: column wrap;
    align-items: center;
    width: 90%;
    margin-right: auto;
    margin-left: auto;
    height: 90%;
    background-color: white;
  }

  form > img[alt="honey"] {
    display: none;
  }

  form > img[alt="default_profile_picture"] {
    box-sizing: border-box;
    margin: 3vh 20%;
    height: 7vh;
  }

  form > input[type="text"],
  form > input[type="email"],
  form > input[type="password"] {
    flex-basis: 8%;
    width: 80%;
    margin-bottom: 3.5vh;
    border-top: none;
    border-right: none;
    border-left: none;
    border-radius: 5px;
    background-color: whitesmoke;
  }

  form > input[type="text"]:focus,
  form > input[type="email"]:focus,
  form > input[type="password"]:focus,
  form > input[type="submit"]:focus {
    outline: 0;
  }

  form > input[type="submit"] {
    box-sizing: border-box;
    width: 30%;
    height: 5vh;
    font-size: 2vh;
    font-weight: bolder;
    background-color: rgba(0, 0, 0, 0.83);
    border: 4px solid black;
    border-radius: 10px;
    color: orange;
  }

  form > input[type="submit"]:disabled {
    background-color: rgb(206, 206, 206);
    color: rgb(160, 160, 160);
    border: 1px solid grey;
    cursor: not-allowed;
    font-weight: lighter;
  }

  input[type="submit"]:hover {
    cursor: pointer;
  }
}
</style>